import React, { Component } from 'react';
import Menu from './components/Menu';
import Home from './components/Home';
import About from './components/About';
import TechStack from './components/TechStack';
import Work from './components/Work';
import Contact from './components/Contact';
import Footer from './components/Footer';
import './App.css';

class App extends Component {

  state = {
    menu:{
      home: true,
      about: false,
      techStack:false,
      work: false,
      contact: false, 
    },
    name: 'Ignacio Merino Arnaiz',
    nickname : 'NachoMerino',
    githubName: 'NachoMerino',
    job: 'Full Stack Developer | React.js | Node.js',
    email: 'nacho@nachomerino.com',
    menuBar: ['Home', 'About', 'Tech Stack', 'Work', 'Contact'],
    socialMedia : [
      ['GitHub', 'https://github.com/NachoMerino','fa-github-square'],
      ['Linkedin', 'https://www.linkedin.com/in/ignaciomerinoarnaiz/','fa-linkedin'],
      ['YouTube', 'https://www.youtube.com/c/desarrolladorweb','fa-youtube-square'],
    ]
  }


  activateHome = (e) => {
    e.preventDefault();
    this.setState({
      menu:{
        home: true,
        about: false,
        techStack:false,
        work: false,
        contact: false
        },
      })
    }
  activateAbout = (e) => {
    e.preventDefault();
    this.setState({
      menu:{
        home: false,
        about: true,
        techStack:false,
        work: false,
        contact: false
        },
      })
    }
  activateTechStack = (e) => {
    e.preventDefault();
    this.setState({
      menu:{
        home: false,
        about: false,
        techStack:true,
        work: false,
        contact: false
        },
      })
    }
  activateWork = (e) => {
    e.preventDefault();
    this.setState({
      menu:{
        home: false,
        about: false,
        techStack:false,
        work: true,
        contact: false
        },
      })
    }
  activateContact = (e) => {
    e.preventDefault();
    this.setState({
      menu:{
        home: false,
        about: false,
        techStack:false,
        work: false,
        contact: true,
        }
      })
    }

  loadLocalStorage = () => {
    const menu = JSON.parse(localStorage.getItem('menu'))
    if(menu){
      this.setState({ menu })
    }
  }

  // this will execute before render()
  componentDidMount(){
     this.loadLocalStorage();
  }

  // triger when render() finished
  componentDidUpdate = () => localStorage.setItem('menu', JSON.stringify(this.state.menu))


  render() {
    let whatToRender;
    if (this.state.menu.home){
      whatToRender=( <Home
        activateContact={this.activateContact}
        job={this.state.job}
        socialMedia={[...this.state.socialMedia]}
        hoverSkills={this.hoverSkills}/> ) 
     } else if(this.state.menu.about){
        whatToRender = <About githubName={this.state.githubName}/>
     } else if(this.state.menu.techStack){
        whatToRender = <TechStack />
     }else if(this.state.menu.work){
        whatToRender = <Work />
     }else if(this.state.menu.contact){
        whatToRender = <Contact email={this.state.email} initMap={this.initMap}/>
     }

    return (
      <React.Fragment>
        <Menu 
          nickname={this.state.nickname}
          menuBar={[...this.state.menuBar]}
          activateHome={this.activateHome}
          activateAbout={this.activateAbout}
          activateTechStack={this.activateTechStack}
          activateWork={this.activateWork}
          activateContact={this.activateContact}
        />
        {whatToRender}
        <Footer />
      </React.Fragment>
    );
  }
}

export default App;


    


