import React from 'react';
import { Button, Form, FormGroup, Label, Input} from 'reactstrap';
import GoogleMaps from './GoogleMaps';

export default class Contact extends React.Component {

  render() {
    return (
      <React.Fragment>
      <section className="contact">
        <div className="contact-text">
        <h1>Contact me</h1>
        <p>If you have any questions, please don’t hesitate to contact using form below…</p>
        <h3>Want to know more? <a href={`mailto:${this.props.email}`}>Email me</a>.</h3>
        </div>
        <Form action={`mailto:${this.props.email}`} method="GET">
          <FormGroup>
            <Label>Subject</Label>
            <Input type="text" name="subject" id="form-subject" placeholder="subject" />
          </FormGroup>
          <FormGroup>
            <Label>Write me anything</Label>
            <Input type="textarea" name="body" id="form-text" placeholder="write me something..."/>
          </FormGroup>
          <Button type="submit" value="Send">Submit</Button>
        </Form>
        <GoogleMaps />
      </section>
      </React.Fragment>
      );
  }
}



