import React from 'react';

export default class About extends React.Component {

  state = {
    githubPict : null,
    bio: null,
  }

  fetchAsync = async () => {
  try {
    const result = await fetch(`https://api.github.com/users/${this.props.githubName}?client_id=a12b6d5ca6b666061f3a&client_secret=4d8bbe0423b48bf1394b5b4194138302ceadc6f1`);
    const data = await result.json();
    console.log(data)
      this.setState(
        { 
          githubPict : data.avatar_url,
          bio: data.bio
        }
      )
      } catch (error) {
        console.log('Error', error);
      }
    };

  componentDidMount(){
     this.fetchAsync();
  }

  render() {
    return (
      <React.Fragment>
      <section className="about">
        <h1>WHO AM I?</h1>
        <div className="pict-wrapper">
          <img className="github-picture" src={this.state.githubPict} alt={this.props.githubName + '-GitHub'}/>
        </div>
        <div className="aboutme-text">
        <p>I'm Ignacio Merino Arnaiz, Full Stack web developer, with knowledge in ReactJS, JQuery, Node.js, MySQL and MongoDB. If you have a project that needs a developer then that's where I come in!</p>
        <p>My job is to build your website so that it is functional and user-friendly but at the same time attractive.</p>
        <p>I also teach both online and face-to-face JavaScript classes, you can find my online courses on Youtube and I teach as a tutor at DCI (Digital Career Institute)</p>
        <p className="personal-phrase">{this.state.bio}</p>
        </div>
      </section>
      </React.Fragment>
      );
  }
}


