import React from 'react';

export default class TechStack extends React.Component {

  render() {
    return (
      <React.Fragment>
      <section className="tech-stack">
            <a frameBorder="0" data-theme="dark" data-layers="1,2,3,4" data-stack-embed="true" href="https://embed.stackshare.io/stacks/embed/2212298ecc0978918b08f9b047a80e" target="_blank">Here you can find my tech stack</a>
      </section>
      </React.Fragment>
      );
  }
}



